

























































































































import { VForm } from "@/store/types";
import CenteredCard from "@/components/CenteredCard.vue";
import Vue from "vue";
import { upload } from "@/api/users";
import { mapActions } from "vuex";

export default Vue.extend({
  components: { CenteredCard },
  data: () => ({
    email: "",
    tab: null,
    filephoto: null as null | string | Blob,
    firstname: null,
    lastname: null,
    password: null,
    show: false,
    valid: true,
    emailRules: [
      (v: string) => !!v || "E-mail is required",
      (v: string) => /.+@.+\..+/.test(v) || "E-mail must be valid",
    ],
    nameRules: [
      (v: string) => !!v || "Name is required",
      (v: string) =>
        (v && v.length <= 10) || "Name must be less than 10 characters",
    ],
  }),

  computed: {
    form(): VForm {
      return this.$refs.form as VForm;
    },
  },

  methods: {
    ...mapActions("user", ["register"]),
    resetForm() {
      this.form.reset();
    },
    submit() {
      if (this.form.validate()) {
        this.registerUser();
      }
    },
    registerUser() {
      this.register({
        email: this.email,
        password: this.password,
        firstname: this.firstname,
        lastname: this.lastname,
      }).then((ok: boolean) => {
        const valid_file = (this.$refs.filephoto as VForm)?.validate() ?? false;
        if (valid_file && this.filephoto && ok) {
          const data = new FormData();
          data.append("image", this.filephoto);
          upload(data).then(() => {
            this.$store.dispatch("alert/success", "Photo added!", {
              root: true,
            });
            this.$router.push("/timeline");
          });
        }
      });
    },
  },
});
